.container-fluid{
	background: #FFEDBC; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-linear-gradient(bottom, #FFEDBC, #0ABFBC);
	background: -o-linear-gradient(bottom, #FFEDBC, #0ABFBC);
	background: linear-gradient(to top, #FFEDBC, #0ABFBC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	height: 50.2em;
	width: 100%;
	
	.return-logo{
		background: none;
		border:none;
		margin-top:1em;
		color: #000;
		font-size:24px;
		
		&:hover{
			color: #FFEDBC;
			-webkit-transition: ease-in-out 0.2s all;
			   -moz-transition: ease-in-out 0.2s all;
			    -ms-transition: ease-in-out 0.2s all;
			     -o-transition: ease-in-out 0.2s all;
			        transition: ease-in-out 0.2s all;
		}

		&:not(:hover){
			-webkit-transition: ease-in-out 0.2s all;
			   -moz-transition: ease-in-out 0.2s all;
			    -ms-transition: ease-in-out 0.2s all;
			     -o-transition: ease-in-out 0.2s all;
			        transition: ease-in-out 0.2s all;
		}
	}
	
	h1{
		font-size:25em;
		color: #FFEDBC;
	}

	h3{
		color: #0ABFBC;
	}

	p{
		.return{
			margin-top:5em;
			border-radius:0px 0px 0px 0px;
			background:none;
			color: #000;
			border-color:#0ABFBC;
			
			&:hover{
				padding:1em;
				color: #FFF;
				background: #0ABFBC;
				border-color: #0ABFBC;
				-webkit-transition: ease-in-out 0.2s all;
				   -moz-transition: ease-in-out 0.2s all;
				    -ms-transition: ease-in-out 0.2s all;
				     -o-transition: ease-in-out 0.2s all;
				        transition: ease-in-out 0.2s all;
			}

			&:not(:hover){
				-webkit-transition: ease-in-out 0.2s all;
				   -moz-transition: ease-in-out 0.2s all;
				    -ms-transition: ease-in-out 0.2s all;
				     -o-transition: ease-in-out 0.2s all;
				        transition: ease-in-out 0.2s all;
			}
		}
	}
}